.footerBlock {
  min-height: 50vh;
  background-color: white;
  padding: 20px 0 50px;

  .footerContainer {
    flex-direction: column;
    justify-content: flex-start;

    .container {
      height: 50px;
      width: 250px;
      display: flex;
      justify-content: space-between;
      margin-bottom: 5px;
    }

    .text {
      text-align: center;
      color: #000000;
      font-family: 'Lato', sans-serif;
      font-size: 15px;
      display: flex;
      align-items: flex-start;
      margin-left: 3px;
    }
  }
}



